@import url(https://fonts.googleapis.com/css2?family=Barlow:ital@0;1&family=Fraunces:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:ital@0;1&family=Fraunces:wght@100;600&display=swap);
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

.main{
    min-height: 100vh;
}

a{
    text-decoration: none;
}
.header{
    background-image: url("/public/images/image-header.jpg");
    background-repeat: no-repeat;
    height: 750px;
    background-size: 100% 100%;

}

.header .nav{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 40px;
}


.header .nav .sunny{
    width: 100px;
}

.header .nav ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    width: 370px;
}

.header .nav ul li a{
    color: hsl(0, 0%, 100%);
    font-family: 'Barlow', sans-serif;
    font-weight: bold;
}

.header .nav ul li .button{
    padding: 10px 20px;
    background-color: hsl(0, 0%, 100%);
    color: hsl(198, 62%, 26%);
    border-radius: 20px;
    font-weight: bold;
}


.header h1{
    text-align: center;
    margin-top: 117px;
    font-family: 'Fraunces', serif;
    color: hsl(0, 0%, 100%);
    font-size: 57px;
    font-weight: bold;
}

@media only screen and (min-width: 769px) {
    .header .nav .humb{
        display: none;
    }
}


@media only screen and (max-width : 768px) {

    .header{
        height: 338px;
    }
    .header .nav ul{
        display: none;
    }

    .header h1{
        font-size: 35px;
        margin: 40px;
    }

    .header .nav .humb:hover{
        cursor: pointer;
    }
}

.tab{
    display: grid;
    grid-template-columns: repeat(2,1fr);
}


.tab div{
    padding: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.tab div h1{
    font-size: 40px;
    font-family: 'Fraunces', serif;
    color:  hsl(212, 27%, 19%);
}


.tab div p{
    font-size: 18px;
    font-family: 'Barlow', sans-serif;
    color: hsl(213, 9%, 39%);
    line-height: 22px;
    margin: 20px 0px;
}

.tab div h4{
    color:hsl(212, 27%, 19%) ;
    font-family: 'Fraunces', serif;
}

.tab img{
    width: 100%;
} 


@media only screen and (max-width: 769px){
    .tab{
        display: flex;
        flex-direction: column;
    }

    .tab div{
        padding: 21px;
        text-align: center;
        height: 315px;
    }
}

.tab2{
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.tab2 .berrys{
    background-image: url("/public/images/image-graphic-design.jpg");
}

.tab2 .orange{
    background-image: url("/public/images/image-photography.jpg");
}

.tab2 :is( .orange,.berrys){
    width: 100%;
    text-align: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 40px 137px;
    height: 530px;

}

:is(.tab2 .orange ,.tab2 .berrys)  p{
    margin-top: 34px;
    font-family: 'Barlow', sans-serif;
}

:is(.tab2 .orange ,.tab2 .berrys) h1{
    font-family: 'Fraunces', serif;
}

.tab2 .orange{
    color: hsl(198, 62%, 26%);
}

.tab2 .berrys{
    color: hsl(167, 40%, 24%);
}


@media only screen and (max-width : 768px) {
    .tab2{
        display: flex;
        flex-direction: column;
    }

    .tab2 :is( .orange,.berrys){
        padding: 40px 48px;
        height :415px;
    }
}
.card{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 50px;
    height: 400px;    
    align-items: center;
    text-align: center;
}

.card img {
    width: 100px;
border-radius: 63px;
}

.card p{
    color: hsl(213, 9%, 39%);
    font-family: 'Barlow', sans-serif;
    font-weight: bold;
    margin: 20px 0;
}

.card div h4{
    font-size: 20px;
    font-family: 'Fraunces', serif;
}

.card div small{
    margin-top: 12px;
    font-family: 'Barlow', sans-serif;
    color: hsl(213, 9%, 39%);
    font-weight: bold;
}

@media only screen and (max-width : 768px) {
    .card{
        height: 305px;
        margin-bottom: 100px;
        padding: 20px 50px;
    }
}
.article h1{
    text-align: center;
    margin: 50px auto;
    font-family: 'Fraunces', serif;
    color: hsl(210, 4%, 67%);
}

.article .clients{
    display: flex;
    justify-content: space-around;
}

@media only screen and (max-width : 768px) {
    .article .clients{
        flex-direction: column;
    }
}
.imgs{
    display: flex;
    margin-top: 160px;
}

.imgs img{
    width: 25%;
}

@media only screen and (max-width : 768px) {
    .imgs{
        display: grid;
        grid-template-columns: 2fr 2fr;
        margin-top: 26px;
    }

    .imgs img{
        width: 100%;
    }
}
.footer{
    background-color: #90d4c5;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px 60px;
    align-items: center;
    height: 350px;
}

.footer ul{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    list-style: none;
    font-family: 'Barlow', sans-serif;
}

.footer ul li a {
    color: white;
    font-weight: bold;
}

.footer small{
    color: white;
    font-weight: bold;

}

.footer small a{
    color: #2c7566;
}
@media only screen and (max-width: 768px) {
    .mobileNav{
        background-color: #3ebfff;
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        transition: all 0.4s ease;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    
    .mobileNav .humb{
        position: absolute;
        right: 50px;
        display: flex;
        flex-direction: column;
        top: 35px;
    
    }

    .mobileNav .humb:hover{
        cursor: pointer;
    }
    
    .span1,.span2{
        background-color: white;
        width: 25px;
        height: 3px;
    }
    
    .mobileNav .humb .span1{
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        position: relative;
        top: 3px;
    }
    
    
    .mobileNav .humb .span2{
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    
    .mobileNav ul{
        display: flex;
        height: 100%;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;list-style: none;
    }
    
    .mobileNav ul li a{
        color: white;
        font-family: 'Barlow', sans-serif;
        font-weight: bold;
        font-size: 20px;
    }

    .mobileNav ul li a:hover{
        cursor: pointer;
    }
    
    
}

@media only screen and (min-width: 769px){
    .mobileNav{
        display: none;
    }
}

.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.close{
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
